<template>
	<div class="article-item">
		<div class="header">{{ article.title || '加载中...' }}</div>
		<div class="info">
			<span>发布日期: {{ format(article.date, 'YYYY-MM-DD HH:mm:ss') }}</span>
			<span>作者: {{ article.author }}</span>
		</div>
		<div class="content" v-html="article.content"></div>
	</div>
</template>

<script>
	import dateformat from '../dateformat'

	export default {
		name: 'articleitem',
		data() {
			return {
				article: ''
			}
		},
		methods: {
			getArticle(cb) {
				let id = this.$route.query.id;
				fetchData({
					url: `./static/data/articles/${id}.json`,
					success(res) {
						cb(res.data)
					},
					fail(err) {
						alert('获取文章内容不成功.')
					}
				})
			},
			format(val, type) {
				return dateformat(val, type)
			}
		},
		created() {
			this.getArticle(data => {
				this.article = data
			})
		}
	}
</script>

<style scoped>
	.info {
		display: flex;
		justify-content: space-around;
		font-size: 13px;
		padding: 10px;
		background: rgba(255, 255, 255, .12);
	}
	.content {
		padding: 10px;
		font-size: 14px;
	}
</style>

<style>
	.content p {
		text-indent: 2em;
	}
</style>


