<template>
  <div id="app">
    <transition name="fade">
    	<router-view class="view" :class="{ blur: isPanelShow }"/>
    </transition>
    <div class="menu" :class="{ toggle: isPanelShow }" @click="isPanelShow = !isPanelShow">
    	<div class="bar"></div>
    	<div class="bar"></div>
    </div>
    <transition name="scale">
    	<div class="back" v-show="$route.name != 'index'" @click="$router.back()">
    		<img src="./assets/icon_back.png">
    	</div>
    </transition>
    <div class="back-drop" v-if="isPanelShow" @click="isPanelShow = false"></div>
    <menu-panel class="hide" :class="{ show: isPanelShow }"></menu-panel>
  </div>
</template>

<script>
import MenuPanel from './components/panel'

export default {
  name: 'App',
  components: { MenuPanel },
  data() {
  	return {
  		isPanelShow: false
  	}
  },
  watch: {
    '$route' (to, from) {
      this.isPanelShow = false
    }
  }
}
</script>

<style>
@import './style/reset.css';
@import './style/global.css';

#app {
	position: relative;
	width: 100vw;
	height: 100vh;
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background: #00171b;
	color: #fff;
	overflow: hidden;
}
.view {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	transition: all .4s;
}
.blur {
	filter: blur(5px);
}
.menu {
	position: absolute;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	width: 8vw;
	height: 8vw;
	max-width: 40px;
	max-height: 40px;
	top: 20px;
	left: 20px;
	z-index: 24;
}
.menu .bar {
	height: 2px;
	background: #fff;
	border-radius: 4px;
	transition: all .4s;
}
.toggle .bar {
	width: 110%;
	transform-origin: 18% 18%;
}
.toggle .bar:first-child {
	transform: rotate(45deg);
}
.toggle .bar:last-child {
	transform: rotate(-45deg);
}
.hide {
	transform: translateX(-50vw);
}
.show {
	transform: translateX(0);
}
.back-drop {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 18;
}
.back {
	position: absolute;
	width: 8vw;
	height: 8vw;
	max-width: 40px;
	max-height: 40px;
	top: 20px;
	right: 20px;
	z-index: 24;
}
</style>



