<template>
	<div class="panel">
		<ul>
			<li>
				<router-link to="/">首页</router-link>
			</li>
			<li>
				<router-link to="/musicplayer">音乐播放器</router-link>
			</li>
			<li>
				<router-link to="/me">关于我</router-link>
			</li>
		</ul>
	</div>
</template>

<script>
	export default {
		name: 'panel'
	}
</script>

<style scoped>
	.panel {
		width: 50vw;
		max-width: 200px;
		height: 100vh;
		position: absolute;
		padding-top: 15vh;
		top: 0;
		left: 0;
		z-index: 20;
		background: #009688;
		transition: all .4s ease;
	}
	ul, li {
		list-style: none;
	}
	a {
		display: block;
		text-decoration: none;
		padding: 15px;
		text-align: center;
		color: #eee;
		transition: all .4s;
	}
	.router-link-exact-active {
		background: rgba(255, 255, 255, .2);
	}
</style>


