<template>
	<div class="music-player">
		<div class="header">音乐播放器</div>
		<div class="main">
			<transition name="fade"><div class="board disk" :class="{ active: isPlaying }" v-show="!lrcShow" @click="lrcShow = true">
				<div class="cover" ref="cover_wrap" :style="{ transform: coverTransform }">
					<img ref="cover_img" :src="cover" :class="{ playing: isPlaying }">
				</div>
			</div></transition>
			<transition name="fade"><div class="board lrc" @click="lrcShow = false" v-show="lrcShow">
				<div class="scroller" :style="scrollOffset">
					<p class="item" v-for="(i, index) in lrc" :class="{ cur: curLrcLine == index }">{{ i.item }}</p>
				</div>
			</div></transition>
		</div>
		<audio :src="songUrl" ref="player" @ended="end" @canplay="audoReady = true"></audio>
		<div class="tool">
			<div class="process">
				<div class="time start">{{ curTime | time }}</div>
				<div class="bar">
					<div class="track" :style="{ width: process }"></div>
				</div>
				<div class="time end">{{ allTime | time }}</div>
			</div>
			<div class="control">
				<a class="btn prev" @click="prevItem"></a>
				<a class="btn paused" :class="{ play: !isPlaying }" @click="play"></a>
				<a class="btn prev next" @click="nextItem"></a>
				<a class="btn list" @click="listShow = true"></a>
			</div>
		</div>
		<div class="song-list" :class="{ show: listShow }">
			<div class="list-header">
				<span class="title">歌曲列表</span>
				<a class="btn hide-list" @click="listShow = false"></a>
			</div>
			<ul>
				<li v-for="(item, index) in songList" @click="playSong(item, index)" :class="{ cur: index == curPlayingSong }">
					<span class="name">{{ item.name }}</span>
				</li>
			</ul>
		</div>
		<div class="cover-bg" :style="{ backgroundImage: 'url(' + cover + ')' }"></div>
	</div>
</template>

<script>
	export default {
		name: 'musicplayer',
		data() {
			return {
				player: null,
				audoReady: false,
				cover: '',
				songUrl: '',
				isPlaying: false,
				coverTransform: '',
				processTimer: null,
				process: 0,
				songList: [
				{
					name: '只要平凡 - 张杰/张碧晨',
					url: 'https://icefee.github.io/blog/static/data/songs/song_1.mp3',
					cover: 'https://icefee.github.io/blog/static/data/covers/cover_1.JPG',
					lrc: './static/data/lrcs/lrc_1.json'
				},
				{
					name: '忽如远行客 - 云の泣',
					url: 'https://icefee.github.io/blog/static/data/songs/song_2.mp3',
					cover: 'https://icefee.github.io/blog/static/data/covers/cover_2.jpg',
					lrc: './static/data/lrcs/lrc_2.json'
				}
				],
				listShow: false,
				curPlayingSong: 0,
				lrcShow: false,
				curTime: 0,
				allTime: 0,
				lrc: [],
				curLrcLine: -1,
				lrcTimer: null,
				/* 用来修复暂停引起的歌词同步问题 */
				lastDelay: 0,
				lastUpdateTime: 0,
				timeOffset: 0
			}
		},
		mounted() {
			this.player = this.$refs.player;

			let songList = this.songList;
			if(songList.length) {
				this.loadSong(songList[0]);
			}
		},
		methods: {
			loadSong(song) {
				this.cover = song.cover;
				this.songUrl = song.url;
				this.audoReady = false;
				this.player.load();

				this.loadLrc(song.lrc);
				this.curLrcLine = -1;
			},
			playSong(song, index) {
				this.loadSong(song);
				this.curPlayingSong = index;
				this.process = 0;
				this.isReady();
				this.isPlaying = true;
			},
			play() {
				if(!this.isPlaying) {
					this.isPlaying = true;
					this.player.play();
					this.updateProcess(true);
					this.updateLrc(true)
				}
				else {
					let cover_wrap = this.$refs.cover_wrap;
					let cover_img = this.$refs.cover_img;

					let cover_wrap_transform = getComputedStyle(cover_wrap).transform;
					let cover_img_transform = getComputedStyle(cover_img).transform;
					this.coverTransform = cover_wrap_transform === 'none' ? cover_img_transform : cover_img_transform.concat(' ', cover_wrap_transform);
					this.isPlaying = false;
					this.player.pause();
					this.updateProcess(false);
					this.updateLrc(false);
				}
			},
			end() {
				if(this.curPlayingSong < this.songList.length - 1) {
					let next = this.curPlayingSong + 1;
					this.playSong(this.songList[next], next);
				}
				else {
					this.isPlaying = false;
					this.process = 0;
					this.updateProcess(false);
				}
			},
			isReady() {
				if(this.audoReady) {
					this.player.play()
					this.updateLrc(true)
					this.updateProcess(true)
				}
				else {
					setTimeout(() => {
						this.isReady()
					}, 20)
				}
			},
			prevItem() {
				let songs = this.songList;
				let songNum = songs.length;
				if(!this.curPlayingSong) {
					let prev = songNum - 1;
					this.playSong(songs[prev], prev);
				}
				else {
					let prev = this.curPlayingSong - 1;
					this.playSong(songs[prev], prev);
				}
			},
			nextItem() {
				let songs = this.songList;
				let songNum = songs.length;
				if(this.curPlayingSong < songNum - 1) {
					let next = this.curPlayingSong + 1;
					this.playSong(songs[next], next);
				}
				else {
					this.playSong(songs[0], 0);
				}
			},
			loadLrc(url) {
				let t = this;
				fetchData({
					url: url,
					success(res) {
						t.lrc = res.lrc
					},
					fail(err) {
						console.log('获取歌词不成功.');
						t.lrc = [{ item: '暂无可用的歌词' }]
					}
				})
			},
			updateLrc(flag) {
				if(flag) {
					this.curLrcLine ++;
					if(this.curLrcLine == this.lrc.length) return;
					let curLineLrc = this.lrc[this.curLrcLine];
					this.lrcTimer = setTimeout(() => {
						this.updateLrc(true)
					}, curLineLrc.delay + this.timeOffset);
					this.lastUpdateTime = this.getTime();
					this.lastDelay = curLineLrc.delay;
				}
				else {
					clearTimeout(this.lrcTimer);
					this.timeOffset = this.lastDelay - (this.getTime() - this.lastUpdateTime);
				}
			},
			getTime() {
				return new Date().valueOf()
			},
			updateProcess(flag) {
				if(flag) {
					this.processTimer = setInterval(() => {
						let player = this.player;
						this.curTime = parseInt(player.currentTime);

						let process = player.currentTime / player.duration;
						this.process = process * 100 + '%'
					}, 15)
				}
				else clearInterval(this.processTimer)
			}
		},
		computed: {
			scrollOffset() {
				let line = this.curLrcLine;
				return { transform: `translateY(calc(20vh - ${line * 30}px))` }
			}
		},
		filters: {
			time(val) {
				let parseNum = num => {
					return num < 10 ? `0${num}` : num
				}
				return parseNum(parseInt(val / 60)) + ':' + parseNum(val % 60)
			}
		},
		watch: {
			audoReady(state) {
				if(state) {
					this.allTime = parseInt(this.player.duration)
				}
			}
		}
	}
</script>

<style scoped>
	.header {
		position: relative;
		z-index: 7;
	}
	.main {
		position: relative;
		height: calc(100vh - 200px);
		z-index: 8;
	}
	.main .board {
		position: absolute;
		left: 50%;
		top: 50%;
		z-index: 8;
	}
	.disk {
		width: 75vw;
		height: 75vw;
		max-width: 250px;
		max-height: 250px;
		padding: 20px;
		border-radius: 100%;
		background: #000;
		border: 20px solid rgba(255, 255, 255, .02);
		box-shadow: 0 0 0 0 #8bc34a;
		will-change: box-shadow;
		transform: translate(-50%, -70%);
		transition: all .4s;
	}
	.disk.active {
		box-shadow: 0 0 20px 0 #8bc34a;
	}
	.cover {
		border-radius: 100%;
		overflow: hidden;
		border: 1px solid #333;
	}
	.playing {
		animation: rotate 20s infinite linear;
	}
	@keyframes rotate {
		100% {
			transform: rotate(1turn)
		}
	}
	.tool {
		position: absolute;
		width: 100%;
		left: 0;
		bottom: 0;
		padding: 10px 0;
		z-index: 150;
	}
	.tool .process {
		padding: 15px 15px 20px;
		display: flex;
		align-items: center;
	}
	.process .bar {
		width: 100%;
		height: 2px;
		background: #fff;
		overflow: hidden;
		margin: 0 5px;
	}
	.bar .track {
		width: 0;
		height: 100%;
		background: #009688;
	}
	.process .time {
		font-size: 13px;
	}
	.tool .control {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.btn {
		width: 12vw;
		height: 12vw;
		max-width: 60px;
		max-height: 60px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: 80% auto;
	}
	.prev {
		background-image: url('../assets/icon_prev.svg');
	}
	.paused {
		margin: 0 3vw;
		background-image: url('../assets/icon_paused.svg');
	}
	.play {
		background-image: url('../assets/icon_play.svg');
	}
	.next {
		transform: rotate(180deg);
	}
	.list {
		position: absolute;
		right: 5vw;
		background-image: url('../assets/icon_songlist.svg');
		background-size: 60% auto !important;
	}
	.song-list {
		position: absolute;
		width: 100%;
		height: 75vh;
		left: 0;
		bottom: 0;
		z-index: 200;
		background: #009688;
		transform: translateY(75vh);
		will-change: transform;
		transition: all .4s;
	}
	.list-header {
		position: relative;
		text-align: center;
		height: 50px;
		line-height: 50px;
		border-bottom: 1px solid rgba(255, 255, 255, .2);
	}
	.hide-list {
		position: absolute;
		top: 50%;
		right: 0;
		transform: translateY(-50%);
		background-image: url('../assets/icon_hide.svg');
		background-size: 25px auto;
	}
	.song-list.show {
		transform: none;
	}
	.song-list ul {
		height: calc(75vh - 50px);
		overflow-y: scroll;
	}
	.song-list ul li {
		padding: 15px 10px;
		font-size: 14px;
		border-bottom: 1px solid rgba(255, 255, 255, .12);
	}
	.song-list ul .cur {
		padding-left: 35px;
		background: url('../assets/icon_sound.png') 5px center no-repeat;
		background-size: 25px auto;
	}
	.cover-bg {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		z-index: 5;
		background-repeat: no-repeat;
		background-size: 300% auto;
		background-position: center center;
		filter: blur(5px);
		opacity: .25;
	}
	.lrc {
		width: 100%;
		height: 80%;
		padding: 15px;
		transform: translate(-50%, -50%);
		overflow: hidden;
	}
	.scroller {
		padding: 10px;
		transition: all .4s;
	}
	.scroller .item {
		font-size: 14px;
		line-height: 30px;
		text-align: center;
		color: rgb(255, 255, 255, .3);
		transition: all .4s;
	}
	.scroller .item.cur {
		color: #fff;
	}
</style>


