<template>
	<div class="articles">
		<div class="header">文章</div>
		<div class="list">
			<ul>
				<li v-for="item in list">
					<router-link :to="'/articleitem?id=' + item.id">
						<div class="date">
							<div class="up">{{ format(item.date, 'DD') }}</div>
							<div class="down">{{ format(item.date, 'MM') }}</div>
						</div>
						<div class="info">
							<div class="title">{{ item.title }}</div>
							<div class="summary">{{ item.summary }}</div>
						</div>
					</router-link>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import dateformat from '../dateformat'

	export default {
		name: 'articles',
		data() {
			return {
				list: []
			}
		},
		methods: {
			getArticles(cb) {
				fetchData({
					url: './static/data/articles.json',
					success(res) {
						cb(res.articles)
					},
					fail(err) {
						alert('获取文章列表不成功.')
					}
				})
			},
			format(val, type) {
				return dateformat(val, type)
			}
		},
		created() {
			this.getArticles(list => {
				this.list = list
			})
		}
	}
</script>

<style scoped>
	.list li {
		list-style-type: none;
		border-bottom: 1px solid rgba(255, 255, 255, .2);
	}
	.list li a {
		display: flex;
		align-items: center;
		text-decoration: none;
		color: #fff;
		padding: 10px;
	}
	.date {
		width: 40px;
		text-align: center;
	}
	.date * {
		font-size: 14px;
		line-height: 20px;
	}
	.date .up {
		background: #009688;
	}
	.date .down {
		border: 1px solid #009688;
	}
	.info {
		width: 100%;
		padding-left: 10px;
	}
	.summary {
		font-size: 13px;
	}
</style>


